<template>
    <div class="absolute bg-white shadow-2xl rounded-lg z-10" 
    style="transform: translate(-50%, -85%); top:50%; left:50%;">
        <div class="relative border-2 border-gray-400 rounded-lg">
            <button type="button" class="absolute right-2 bg-red text-white rounded px-2 top-2"
            @click="closeForm"><i class="fa fa-times"></i></button>
            <form action="" class="flex flex-col px-3 py-4" 
            @submit="submitFormVendor">        
                <h2 class="py-2 font-bold">Formulario de Vendedor</h2>       
                <section class="flex gap-3">
                    <div class="flex flex-col w-5/12">
                        <div class="flex flex-row items-center my-3 mx-2">
                            <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Nombres *</label>
                            <input type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm "
                            v-model="formDataV.first_name" required>
                        </div>                
                        <div class="flex flex-row items-center my-3 mx-2">
                            <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Apellidos *</label>
                            <input type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm "
                            v-model="formDataV.last_name" required>
                        </div> 
                        <div class="flex flex-row items-center my-3 mx-2">
                            <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Genero *</label>
                                <Multiselect
                                v-model="formDataV.gender" 
                                mode="single"
                                :searchable="true"                                               
                                :options="genders"
                                placeholder="Seleccione genero *"      
                                :classes="select_style_opt"
                                />
                        </div> 
                    </div>
                    <div class="flex flex-col w-5/12">                                                     
                        <div class="flex flex-row items-center my-3 mx-2">
                            <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">EMail *</label>
                            <input type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"                                    
                            :readonly="(mode != 'create')"
                            v-model="formDataV.email" required>
                        </div>
                        <div class="flex flex-row items-center my-3 mx-2">
                            <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Teléfono</label>
                            <input type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm "
                            v-model="formDataV.phone">
                        </div>                              
                    </div>                                             
                </section>                                  
                <section class="flex flex-col">               
                    <div class="flex flex-row items-center my-3 mx-2">
                        <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Dirección</label>
                        <input type="text" class="w-2/4 border mt-1 block py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm "
                        v-model="formDataV.address">
                    </div>
                    <div class="flex flex-row items-center my-3 mx-2">
                        <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">País *</label>
                        <Multiselect
                            v-model="formDataV.country" 
                            mode="single"
                            :searchable="true"                                               
                            :options="countries"
                            placeholder="Seleccione País *"      
                            :classes="select_style_opt"
                        />
                    </div> 
                    <div class="flex">               
                        <div class="flex flex-row items-center my-3 mx-2">
                            <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Ciudad</label>
                            <input type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm "
                            v-model="formDataV.city">
                        </div>
                        <div class="flex flex-row items-center my-3 mx-2">
                            <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Provincia</label>
                            <input type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm "
                            v-model="formDataV.province">
                        </div>       
                        <div class="flex flex-row items-center my-3 mx-2">
                            <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Código Postal</label>
                            <input type="text" class="border mt-1 block w-1/5 py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm "
                            v-model="formDataV.zip_code">
                        </div>   
                    </div>                         
                </section>                       
                <section class="flex flex-col ">
                    <!-- PASSWORD RESET OPTIONS -->
                    <div class="flex flex-col p-5 mt-3">
                        <div class="flex flex-row gap-x-3 items-center my-3 mx-2">
                            <label class="uppercase block text-sm font-medium text-gray-700 mx-2 ">Contraseña</label>
                            <input 
                            :type="(showpass) ? 'text' : 'password'" 
                            class="flex-1 border mt-1 block w-90x100 py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm "
                            v-model="password">
                            <label class="uppercase block text-sm font-medium text-gray-700 mx-2 ">Confirmar Contraseña</label>
                            <input 
                            :type="(showpass) ? 'text' : 'password'" 
                            class="flex-1 border mt-1 block w-full border-gray-400 bg-white rounded-md shadow-sm py-2 px-3
                            focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm "
                            v-model="repassword">
                            <div class="flex items-center gap-x-4">                    
                                <button type="button" @click="showpass=!showpass" v-show="!showpass"><i class="fas fa-eye-slash"></i></button>
                                <button type="button" @click="showpass=!showpass" v-show="showpass"><i class="fas fa-eye"></i></button>                                                
                            </div>                        
                        </div>    
                        <div class="flex justify-between">
                            <small class="text-red" v-show="!samepass">Las contraseñas deben ser iguales.</small>               
                            <!-- <small class="text-green" v-show="samepass">Las contraseñas deben ser iguales.</small>                -->
                            <button type="button" class="ml-auto">Resetear Contraseña</button>
                        </div>
                    </div>  

                    <!-- DISCOUNT PERCENT -->
                    <div class="flex flex-col border rounded-xl gap-3 p-4">
                        <h3 class="uppercase">Margen por productos (%)</h3>
                        <div class="flex flex-row items-center my-3 mx-2">
                            <div v-for="p in available_products" :key="p.id">
                                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">{{ p.name }}</label>
                                <input type="text" class="w-2/5 border mt-1 block py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm "
                                v-model="p.agent_margin">
                            </div>
                        </div>
                    </div>
                </section>    
                <!-- Button -->
                <section class="flex gap-3 mt-10 flex-row-reverse gap-y-4 w-full">
                    <button type="submit" class="inline-flex border border-transparent h-10 bg-orange_1 rounded-md font-semibold text-xs text-white uppercase tracking-widest px-4 py-2 
                        hover:bg-amber-300 
                        focus:outline-none                           
                        active:bg-green-600 
                        disabled:opacity-25 transition"
                        :disabled="load">
                        Salvar
                        <span :class="{'hidden': !load}"><i class="fa fa-spinner fa-pulse"></i></span>
                        </button>            
                    <button type="button" class="inline-flex border border-gray-400 h-10 bg-indigo-900 rounded-md font-semibold text-xs text-white uppercase tracking-widest shadow-sm px-4 py-2
                        hover:text-white hover:bg-indigo-400 
                        focus:outline-none
                        active:text-gray-800 active:bg-indigo-900 
                        disabled:opacity-25 transition"
                        @click="initForm"
                        >Cancelar</button> 
                    <div v-if="error_message" class="error-message items-start">
                        <i class="fa fa-times-circle"></i>
                        <h5 class="text-xl self-center px-2">
                            Error enviando informacion. No se pudo crear el nuevo usuario
                            <p>{{message_text}}</p>
                        </h5>
                        </div>
                        <div v-if="success_message" class="success-message">
                        <i class="fa fa-check-circle"></i>
                        <h5 class="text-xl self-center px-2">La información ha sido enviada satisfactoriamente.</h5>
                    </div>
                </section> 
            </form>
        </div>
    </div>
</template>
<script>
import Authorization from "@/services/user/auth.service";
import ClientService from "@/services/user/client.service";
import ProductTypeService from "@/services/common/product_type.service";

// import Accordion from "@/components/accordion";
// import AccordionItem from "@/components/accordion-item";
import Multiselect from '@vueform/multiselect'
import '@vueform/multiselect/themes/default.css'

export default {
    name: "NewVendor",  
    props: {
        countries: {default: []},
        genders: {default: []},
        vendorId: {default: ''},
    },
    components: { 
        Multiselect,
        // Accordion,
        // AccordionItem,
        // AdminTable
    },
    data() {
        return {      
            title: 'Client',
            mode: 'create',
            message_text: '',
            error_message: false,
            success_message: false,
            select_style_opt: {
                container: 'relative w-6/12 flex items-center justify-end box-border cursor-pointer border border-[#9CA3AF] rounded-lg bg-white text-sm leading-snug outline-none',
                search: 'w-4/12 absolute inset-0 outline-none focus:ring-0 appearance-none box-border border-8 border-transparent text-sm font-sans bg-white rounded-lg pl-2 rtl:pl-0 rtl:pr-3.5',                                                        
                optionSelected: 'text-white bg-light_gray_1',
                optionSelectedPointed: 'text-white bg-light_gray_1 opacity-90',
                optionSelectedDisabled: 'text-green-100 bg-light_gray_1 bg-opacity-50 cursor-not-allowed',
            },            
            available_products: [],
            showpass: false,
            samepass: true,
            load: false,  
            password: '',
            repassword: '',
            company: '',
            IATA: '',            
            formDataV: {
                gender: {
                    value: '',
                    label: ''
                },
                country: {
                    value: '',
                    label: ''
                },
            },
        }
    }, 
    created() {
        this.isAdmin = Authorization.validateRole('ROLE_SuperAdmin')
        ProductTypeService.getCustomList().then(p => this.available_products = p)
        if(this.mode != 'create')
            this.initForm()
    }, 
    watch: {
        vendorId(id) {            
            // Validar si el vendor esta vacio
            if(id) {
                ClientService.getClient(id).then(v => {                    
                    this.formDataV = {
                        //general info
                        first_name: v.first_name,
                        last_name: v.last_name,
                        phone: v.phone,
                        email: v.email,
                        gender_id: v.gender_id,
                        gender: v.gender_id,
                        password: '',                
                        //location
                        address: v.address,
                        city: v.city,
                        province: v.province,
                        zip_code: v.zip_code,
                        //agency     
                        country_id: v.country_id,
                        country: v.country_id, 
                        parent_id: 0
                    }
                    console.log(this.formDataV.country)                    
                    if(v.margin.length > 0)
                        this.available_products = v.margin                    
                    this.mode = 'modify'                    
                    
                })
            } else {
                this.initForm()
            }
        },
        repassword() {        
            this.samepass = (this.repassword == this.password || (this.repassword == '' && this.password == ''))
        } 
    },
    methods: {
        submitFormVendor(e) {
            e.preventDefault();  

            if(this.validate(this.formDataV.first_name) && this.validate(this.formDataV.last_name) && this.validate(this.formDataV.gender) &&
            this.validate(this.formDataV.email) && this.validate(this.formDataV.country)) {                   
                this.formDataV.password = this.password
                this.formDataV.gender_id = (typeof this.formDataV.gender == 'object') ? this.formDataV.gender.value : this.formDataV.gender
                this.formDataV.country_id = (typeof this.formDataV.country == 'object') ? this.formDataV.country.value : this.formDataV.country
                this.formDataV.parent_id = localStorage.getItem('id')

                this.load = true  
                this.formDataV.margin = []                
                for(let p of this.available_products) {
                    this.formDataV.margin.push({                        
                        id: p.id,
                        agent_margin: p.agent_margin
                    })
                }

                if(this.vendorId) {
                    this.formDataV.id = this.vendorId
                }  

                this.message_text = 'No se pudo realizar la operacion'     
                console.log(this.formDataV);                         
                ClientService.addUpdVendor(this.formDataV).then(res => {
                    this.load = false
                    if(res) {
                        this.mode = 'create'                        
                        this.error_message = false
                        this.success_message = true
                        this.message_text = 'La operacion se realizo con exito!!!'    
                        this.$emit('updList')                    
                        setTimeout(() => {
                            this.success_message = false
                            this.initForm()
                        }, 8000); 
                    }                    
                }).catch(err => {
                    this.load = false
                    console.log(err)
                    this.error_message = true
                    this.success_message = false
                    this.$emit('updList')
                    setTimeout(() => {
                        this.error_message = false
                    }, 8000); 
                })
            } else {                                  
                this.error_message = true
                this.success_message = false
                setTimeout(() => {
                    this.error_message = false
                }, 8000); 
            }
        },
        initForm() {
            this.password = ''
            this.repassword = ''
            this.$emit('clearId')            
            this.formDataV = {
                from_web: true,
                //general info
                first_name: '',
                last_name: '',
                phone: '',
                email: '',          
                gender_id: '',                
                password: '',                
                //location
                address: '',
                city: '',
                province: '',
                zip_code: '',
                //agency        
                iata: '',
                company: '',
                is_agent: '',              
                country_id: '',                
                margin: []
            }   
            ProductTypeService.getCustomList().then(p => this.available_products = p)  
            this.closeForm()
        },
        isOK() {
            return (!this.error_message && this.success_message) ? 'bg-green-400' : 'bg-red'           
        },        
        validate(value) {
            if (value == '')
                return false
            else if(value === undefined)
                return false
            else if(value === null)
                return false
            return true
        }, 
        closeForm() { this.$emit('closeForm') }
    }
}
</script>
<style scoped>
    
</style>